import * as React from 'react';
import { MDXProvider } from "@mdx-js/react"
import MDXButton from "./MDXButton"
import MuiLink from '@mui/material/Link';
import ResultFooter from './ResultFooter';
import Typography from '@mui/material/Typography';

// компоненты, которые доступны в mdx без импорта
const components = {
  ResultFooter,
  Note,
  Button: MDXButton,
  a: MuiLink,
  blockquote: props => <blockquote style={{ color: 'grey'}} {...props}/>,
};

export default function MyMDXProvider({ children }) {
  return (
    <MDXProvider components={components}>{children}</MDXProvider>
  )
}

function Note({ children }) {
  return (
    <Typography paragraph variant="body1" color="text.secondary" sx={{mt: 3}}>
      {children}
    </Typography>
  );
}
